#menu {
	padding: 20px 1%;
	max-width: 30vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	box-shadow: inset -5px 0 5px rgba(0, 0, 0, 0.075);
	min-height: 90vh;
}

.btn {
	background-color: snow;
	border: none;
	box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.35);
	font-family: "Google Sans";
	font-size: 15px;
	border-radius: 5px;
	padding: 8px 16px;
	cursor: pointer;
}

.btn:hover {
	background-color: #f0f0f0;
}

.btn:active {
	background-color: #e0e0e0;
}