.task {
	padding: 5px 10px;
	border-radius: 8px;
	width: 15vw;
	cursor: pointer;
	margin-bottom: 7px;
	box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0);
	border: 1px solid transparent;
	color: #aaa;
	min-height: 20px;
}

.task:hover {
	color: #000;
	transition: 0.3s;
}

.task:active {
	background-color: #f0f0f0;
}

.selected {
	color: #000;
	box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25);
	transition: 0.3s;
}