#details-wrap {
	flex-grow: 1;
	position: sticky;
	top: 0;
	height: 82vh;
	padding: 35px 5%;
}

#details input {
	padding: 0;
	font: 700 3rem "Google Sans";
	border: none;
	margin: 30px 0;
}

#details textarea {
	padding: 0;
	font: 400 1.25rem "Google Sans";
	border: none;
	width: 100%;
	outline: none !important;
}

#details input:focus {
	outline: none;
}


#details #title-wrap {
	display: flex;
	gap: 15px;
}

#details input[type="checkbox"] {
	width: 35px;
	border-radius: 50%;
}

.off {
	display: none;
}